.container {
  display: flex;
  flex-direction: row;
  width: 100%;
  min-height: 100vh;
}

.wrap_left {
  flex: 6;
  background-color: #bbb;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: relative;
}

.mockup_wrap {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

/* ê¸°ë³¸ ì„¤ì • */
.mockup_inner {
  position: absolute;
  margin-bottom: 20%;
}

.mockup_frame {
  position: relative;
  width: 100%;
  height: auto;
}

.mockup_img {
  width: 100%;
  height: auto;
  display: block;
}

.imac {
  width: 35vw;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
}

.imac .inner_frame {
  top: 3%;
  left: 2%;
  width: 96%;
  height: 65%;
}

.inner_frame {
  position: absolute;
  overflow: hidden;
}

.inner_img {
  width: 100%;
  /* position: absolute;
  top: 100%;
  left: 0;
  transition: top 0.6s ease-in-out; */
  transform: translateY(0%);
  transition: transform 1s ease;
}

.imac .inner_frame:hover .inner_img {
  transform: translateY(-69%);
}

.inner_bg {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 30%;
  width: 100%;
  background-color: #f6f6f6;
  z-index: 1;
}

.wrap_right {
  flex: 4;
  background-color: #fff;
  position: relative;
  padding: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.Category {
  position: absolute;
  left: -4.5rem;
  background-color: #fff;
  padding: 1rem 1.5rem;
  border-radius: 15px 15px 0 0;
  transform: rotate(270deg);
  color: #bbb;
  font-weight: bold;
  font-size: 1.1rem;
  z-index: 5;
  cursor: pointer;
}

.Category a {
  text-decoration: none;
  color: inherit;
}

.website {
  top: 30%;
}
.process {
  top: 60%;
}

.text_wrap {
  color: #262626;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.text_wrap h2 {
  font-size: 2rem;
  margin: 0;
  color: #e81a0d;
}

.mine_title {
  margin: 0;
}

.mine_title,
.text_wrap .type,
.text_wrap .info,
.text_wrap .font,
.text_wrap .color {
  font-size: 0.8rem;
  line-height: 1.6;
}

.skills_icons {
  display: flex;
  gap: 1rem;
}

.skill_icon {
  position: relative;
  display: inline-block;
}

.skill_icon img {
  width: 40px;
  height: auto;
  border-radius: 15px;
  box-shadow: 3px 3px 2px 1px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease;
  cursor: pointer;
}

.skill_icon img:hover {
  transform: scale(1.1);
}

.tooltip {
  position: absolute;
  bottom: -1.5rem;
  left: 50%;
  transform: translateX(-50%);
  background: #121b5d;
  color: white;
  padding: 4px 8px;
  font-size: 0.75rem;
  border-radius: 6px;
  white-space: nowrap;
  opacity: 0;
  transition: opacity 0.2s ease;
  pointer-events: none;
  z-index: 99;
}

.skill_icon:hover .tooltip {
  opacity: 1;
}

/* COLORì¹© ìŠ¤íƒ€ì¼ */
.color {
  display: flex;
  gap: 1rem;
  color: #fff;
}
.color div {
  padding: 0.5rem 1rem;
  border-radius: 8px;
  font-weight: bold;
}
.color div:nth-child(1) {
  background-color: #111;
}
.color div:nth-child(2) {
  background-color: #fff;
  color: #111;
  border: 1px solid #111;
}
.color div:nth-child(3) {
  background-color: #e81a0d;
}

@media (max-width: 768px) {
  .container {
    flex-direction: column;
  }

  .wrap_left,
  .wrap_right {
    width: 100%;
    height: auto;
    padding: 1.5rem;
  }

  .mockup_wrap {
    flex-direction: column;
    align-items: center;
  }

  .mockup_inner {
    margin: 1rem 0;
    max-width: 70%;
  }

  .Category {
    left: 0;
    transform: rotate(0deg);
    padding: 0.5rem 1rem;
    font-size: 1rem;
  }

  .website {
    top: 1rem;
  }
  .process {
    top: 3.5rem;
  }

  .text_wrap h2 {
    font-size: 1.5rem;
  }

  .color div {
    font-size: 0.9rem;
  }
}
